import { useTranslation } from "react-i18next";
import style from "../JoiningForm/JoiningForm.module.css";
import { useHostelApplyPinnedApplicationList } from "../optimized/Hooks/ApiHooks/HostelManager/hostel-manager-api";
import Loading from "../optimized/Loader/Loading";
import { assestsUtilityUrl } from "../optimized/Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";

function Card({ applyData, setIndex, setOtherData }) {
  const { t } = useTranslation();
  const [step, setStep] = useState(false);

  return (
    <>
      {applyData?.type ? (
        <div className={style.card} onClick={() => setStep((pre) => !pre)}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "flex-start",
              gap: "1rem",
            }}
          >
            <img src={`${assestsUtilityUrl}/Vector.svg`} alt="admission icon" />

            <div
              style={{
                width: "100%",
              }}
            >
              <div
                className={style.cardRow}
                style={{
                  justifyContent: "flex-start",
                  gap: "1rem",
                  alignItems: "center",
                }}
              >
                <h6
                  style={{
                    marginBottom: "0",
                  }}
                >
                  {applyData?.type ?? ""}
                </h6>
                <img
                  src={`${assestsUtilityUrl}/next.svg`}
                  alt="admission icon"
                  style={{
                    rotate: "90deg",
                    height: "1.3rem",
                  }}
                />
              </div>
            </div>
          </div>
          {step ? (
            <>
              {applyData?.apps?.map((appD) => (
                <div
                  onClick={() => {
                    setIndex(7);
                    setOtherData(appD);
                  }}
                  style={{
                    marginTop: "0.7rem",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "flex-start",
                    gap: "1rem",
                    marginLeft: "2rem",
                    borderTop: "0.5px solid #cacaca",
                    paddingTop: "0.7rem",
                  }}
                >
                  <img
                    src="/images/members/admission-application-icon.svg"
                    alt="admission icon"
                  />
                  <div
                    style={{
                      width: "100%",
                    }}
                  >
                    <div className={style.cardRow}>
                      <h6>{appD?.applicationName}</h6>

                      {appD?.application_type === "Direct Admisison" && (
                        <img
                          src="/images/members/application-hotspot-icon.svg"
                          alt="admission icon"
                        />
                      )}
                    </div>

                    <div className={style.cardRow}>
                      <p>{appD?.applicationUnit?.hostel_unit_name ?? ""}</p>
                      {appD?.applicationEndDate && (
                        <p
                          style={{
                            marginRight: "2.3rem",
                          }}
                        >
                          {t("last_date_to_apply")}
                        </p>
                      )}
                    </div>
                  </div>
                </div>
              ))}
            </>
          ) : null}
        </div>
      ) : (
        <div
          className={style.card}
          onClick={() => {
            setIndex(7);
            setOtherData(applyData);
          }}
        >
          <div className={style.card_row_container}>
            <img
              src="/images/members/admission-application-icon.svg"
              alt="admission icon"
            />
            <div
              style={{
                width: "100%",
              }}
            >
              <div className={style.cardRow}>
                <h6
                  style={{
                    marginBottom: "0.2rem",
                  }}
                >
                  {applyData?.applicationName}
                </h6>
                {applyData?.application_type === "Direct Admisison" ? (
                  <div className={style.hostel_direct}>
                    {applyData?.applicationEndDate ? (
                      <h6
                        style={{
                          marginBottom: "0.2rem",
                          marginRight: "1rem",
                        }}
                      >
                        {applyData?.applicationEndDate}
                      </h6>
                    ) : (
                      ""
                    )}
                    {applyData?.application_type === "Direct Admisison" && (
                      <img
                        src="/images/members/application-hotspot-icon.svg"
                        alt="admission icon"
                      />
                    )}
                  </div>
                ) : applyData?.applicationEndDate ? (
                  <h6
                    style={{
                      marginBottom: "0.2rem",
                      marginRight: "2.3rem",
                    }}
                  >
                    {applyData?.applicationEndDate}
                  </h6>
                ) : (
                  ""
                )}
              </div>
              <div className={style.cardRow}>
                <p>{applyData?.applicationUnit?.hostel_unit_name ?? ""}</p>
                {applyData?.applicationEndDate && (
                  <p
                    style={{
                      marginRight: "2.3rem",
                    }}
                  >
                    {t("last_date_to_apply")}
                  </p>
                )}
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

function HostelApplyApplication({ instituteId, setIndex, setOtherData }) {
  const { t } = useTranslation();

  const {
    hostelApplyPinnedApplicationList,
    hostelApplyPinnedApplicationListLoading,
  } = useHostelApplyPinnedApplicationList({
    data: {
      id: instituteId,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !instituteId,
  });

  console.info(
    "hostelApplyPinnedApplicationList",
    hostelApplyPinnedApplicationList
  );

  return (
    <div className={style.applications}>
      <div className={style.top}>{t("join_or_apply_instutude")}</div>
      <div className={style.content}>
        <div className={style.contentBottom}>
          <h6>{t("new_application")}</h6>
          <div className={style.cardsContainer}>
            {hostelApplyPinnedApplicationList?.list?.map((newApp) => (
              <Card
                key={newApp?._id}
                applyData={newApp}
                setIndex={setIndex}
                setOtherData={setOtherData}
              />
            ))}
            {hostelApplyPinnedApplicationListLoading && <Loading />}
          </div>
        </div>
      </div>
    </div>
  );
}

export default HostelApplyApplication;
